html,
body,
#root,
#root > div {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root canvas {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}
body {
  color: white;
}

.centerMain {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.content {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  font-size: 1.5rem;
}
.large.text {
  font-size: 3em;
}
.small.text {
  font-size: 2rem;
}

